<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.title{
    text-align:center; 
    font-size:30px;
}
.caption{
    text-align:left; 
    font-size:20px;
}
.input{
    width:400px;
    border-color:black;
}
.button{
    font-size:30px;
    width:400px;
    background-color:#439EFF;
    color:black;
}
.src{
    border: none;
    color: black;
    background-color: white;
    cursor: pointer;
    text-decoration:underline;
    font-size:20px;
}

</style>

<template>
  <div style="margin-top:-50px">
    <img src="../assets/eastek.png" />
    <el-button @click="clickJobMatrix"> Job Matrix </el-button>

    
    <div style="position:relative;">
      <div style="display: flex;">
        <div  v-for="layer in drillLayers" :key="layer" style="width:25px; z-index:2;">
            <el-slider 
              vertical
              v-model="layer.fake_drl"
              range
              :height="sliderHeight"
              :min="0"
              :max="layerNum"
              :marks = "marks"
              :show-tooltip = "false"
              @change="slide"
            >
            </el-slider>
        </div>
      </div>

      
      <el-table 
        :data="layersList"
        :height="tableHeight"
        style="position:absolute; top: -48px;" 
        :show-header="true" 
        border 
        :row-style="{height: '30px'}"
        :cell-style="{padding: '0', 'font-size': '20px'}"
      >
        <el-table-column prop="" label="" :width="blankWidth" />
        <el-table-column prop="name" label="name" width="300px" show-overflow-tooltip >
          <template slot-scope="scope">
            <div style="position: relative;">
              {{scope.row.name}}
              <span style="position: absolute; right: 0px;">({{scope.row.short_type}}, {{scope.row.short_polarity}})</span>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="inputFile" label="pcb" width="600px" show-overflow-tooltip />
        <el-table-column prop="inputFile" label="ori" width="600px" show-overflow-tooltip />
      </el-table>
    </div>

    <div style="position:absolute; right:0px; top: 30%;">
      <el-table  :data="drillLayers" border :cell-style="{'font-size': '20px'}">
        <el-table-column prop="name" label="drl_start"  width="300" />
        <el-table-column prop="fake_drl[0]" label="drl_start" width="100" >
          <template slot-scope="scope">
              {{drillMap[scope.row.fake_drl[1]-1]}}
          </template>
        </el-table-column>
        <el-table-column prop="drillMap.fake_drl[1]" label="drl_end" width="100" >
          <template slot-scope="scope">
              {{drillMap[scope.row.fake_drl[0]]}}
          </template>
        </el-table-column>
      </el-table>
    </div>
    
  
  </div>
</template>

<script>
export default {
  name: 'login',
