""},{"name":"drill-bd-1-9.art","type":"document","context":"misc","polarity":"positive","side":"none","row":30,"drl_start":"","drl_end":"","inputName":"drill-bd-1-9.art","selected":false,"inputFile":"drill-bd-1-9.art","work":false,"affected":false,"display":false,"display_number":0,"display_color":""}],
            drillLayers:[],
            drillMap:{},
        }
  },
  created(){
        this.$router.replace({query: ''}).catch(()=>{});

        this.layerNum = this.layersList.length;
        this.tableHeight = this.layerNum * 30 + 50 + 'px';
        this.sliderHeight = this.layerNum * 30 + 'px';
        var shortTypeMap = {'solder_paste':'sp', 'silk_screen':'ss', 'solder_mask':'sm', 'signal':'sig', 'drill':'drl', 'rout':'rt', 'document':'doc'};
        var shortPolarityMap = {'positive':'p', 'negative': 'n'};

        for(i = 0; i <= this.layerNum; i++){
          this.marks[i] = ''
          if(i>0)
            this.drillMap[this.layerNum-i] = this.layersList[i-1].name
        }
        this.drillMap[i] = this.layersList[i-2].name

        this.layersList.forEach(item=>{Object.assign(item, {
          'short_type':shortTypeMap[item.type],
          'short_polarity':shortPolarityMap[item.polarity],
        })})
        var temp = this.layersList.filter(function (layer) {return layer.type == 'drill' || layer.type == 'rout';})
        temp.forEach(item=>{Object.assign(item, {
          'fake_drl':[ Object.keys(this.drillMap).find(key => this.drillMap[key] == item['drl_start'])+1, 
                       Object.keys(this.drillMap).find(key => this.drillMap[key] == item['drl_end'])
                     ]
        })})
        this.drillLayers = JSON.parse(JSON.stringify(temp))
        var i = 0
        
        this.blankWidth = this.drillLayers.length * 30
        console.log(this.drillMap)
        console.log(this.drillLayers)

  },
  mounted() {//監聽enter 若按enter則觸發login
    let _this = this;
    document.onkeydown = function(e) {
        if (_this.$route.name == "login") {
            let key = window.event.keyCode;
            if (key == 13) 
                _this.login();
            }
    }
  },
  computed:{
    
  },
  methods:{
    clickJobMatrix(){
      
      this.jobMatrixDialog = true;
    },
    slide(a){
        console.log(a)
        console.log(this.drillLayers)
    },
  }
}
</script>

